@media print {
  /* your print styles */
  .raleigh-campaign.Campaign.CampaignType--floating div,
  .site-header .header-right-column,
  #page .site-main .post-nav-wrapper,
  #page .site-main .social-share-wrapper,
  aside.sidebar .location-callout-box-basic,
  aside.sidebar .featured-posts-block,
  aside.sidebar .categories-block,
  #page .site-main .call-to-action,
  footer.primary-footer {
    display: none;
  }
}

.entry-title {
  text-align: left;
}

#select-property {
  display: none;
}

#select-property-cities {
  display: none;
  padding: 5px 90px 5px 10px !important;
}

.calculator-content-container {
  max-width: 100%;
  margin: 3.125rem auto;
  margin-bottom: 0.9375rem;
}

.calculator-content-container a {
  color: #084C61;
}

.calculator-content-container a:hover {
  color: #053b4c;
}

.calc-calculator-wrapper {
  max-width: 100%;
  margin: 3.125rem auto;
  margin-bottom: 0.9375rem;
  overflow: auto;
  border-radius: 15px;
}

.calc-calculator-wrapper input, .calc-calculator-wrapper select {
  padding: 6px 10px;
  border: 1px solid #E9EBEB;
  border-radius: 4px;
}

.calc-calculator-wrapper input {
  width: 120px;
}

.calc-calculator-wrapper input[type=number]::-webkit-inner-spin-button, .calc-calculator-wrapper input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.calc-calculator-wrapper .tooltip {
  background-color: #084C61;
  height: 26px;
  text-align: center;
  width: 26px;
  border-radius: 20px;
  color: white;
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  display: inline-block;
  position: relative;
  opacity: 1 !important;
}

.calc-calculator-wrapper .tooltip .tooltiptext {
  background-color: #f4f4f4;
  border-radius: 1px;
  border: 1px solid #E9EBEB;
  color: #3f3f3f;
  font-weight: normal;
  left: 40px;
  padding: 5px 0;
  position: absolute;
  text-align: center;
  top: -30px;
  visibility: hidden;
  width: 220px;
  z-index: 1;
  -webkit-box-shadow: 10px 15px 9px -10px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 10px 15px 9px -10px rgba(0, 0, 0, 0.05);
  box-shadow: 10px 15px 9px -10px rgba(0, 0, 0, 0.05);
}

.calc-calculator-wrapper .tooltip:hover .tooltiptext {
  visibility: visible;
}

.calc-calculator-wrapper #budget-calculator {
  width: 100%;
}

.calc-calculator-wrapper #budget-calculator thead {
  background-color: #084C61;
  border-collapse: collapse;
  border: 0px;
  color: #FFFFFF;
  font-size: 21px;
  font-weight: bold;
}

.calc-calculator-wrapper #budget-calculator thead th {
  padding: 16px 20px;
}

.calc-calculator-wrapper #budget-calculator tbody tr td {
  padding: 6px 10px;
  border-bottom: 1px solid #E9EBEB;
  border-collapse: collapse;
  padding: 10px 20px;
  text-align: center;
}

.calc-calculator-wrapper #budget-calculator tbody tr td:first-child {
  text-align: left;
}

.calc-calculator-wrapper #budget-calculator tbody tr td:last-child {
  background-color: rgba(233, 235, 235, 0.3);
}

.calc-calculator-wrapper #budget-calculator tbody tr td.pad-left {
  padding-left: 25px;
}

.calc-calculator-wrapper #budget-calculator .calc-total {
  background-color: #E9EBEB;
  border-collapse: collapse;
  border: 0px;
  color: #084C61;
  font-size: 21px;
  font-weight: bold;
  line-height: 60px;
}

.calc-calculator-wrapper #budget-calculator .calc-total .pad-left {
  background-color: #177E89;
  color: #FFFFFF;
}

#monthly-present, #monthly-sls {
  font-weight: bold;
  padding: 6px 10px;
}

#monthly-sls {
  background-color: #084C61 !important;
}

@media (max-width: 767.98px) {
  .calc-calculator-wrapper select {
    display: block;
    width: 100%;
    margin-bottom: 10px;
    padding: 15px 20px;
  }
  .calc-calculator-wrapper #budget-calculator {
    border-bottom: 1px solid #E9EBEB;
    min-width: 100%;
  }
  .calc-calculator-wrapper #budget-calculator tbody tr td, .calc-calculator-wrapper #budget-calculator #budget-calculator thead tr th {
    padding: 10px 30px;
    text-align: right;
    border-bottom: 0px;
  }
  .calc-calculator-wrapper #budget-calculator tbody tr td.pad-left, .calc-calculator-wrapper #budget-calculator #budget-calculator thead tr th.pad-left {
    padding-left: 10px;
  }
  .calc-calculator-wrapper input[type="number"], .calc-calculator-wrapper textarea {
    width: 30%;
    text-align: right;
    float: right;
    margin-top: -7px;
  }
  .calc-calculator-wrapper table {
    width: 100%;
  }
  .calc-calculator-wrapper thead {
    display: none;
  }
  .calc-calculator-wrapper th.monthly-expenses {
    width: 100%;
    text-align: left;
    font-weight: 600;
    background-color: #084C61;
  }
  .calc-calculator-wrapper th.monthly-present, .calc-calculator-wrapper th.monthly-sls {
    width: 100%;
  }
  .calc-calculator-wrapper td {
    text-align: right;
    display: block;
  }
  .calc-calculator-wrapper td.monthly-expenses {
    width: 100%;
    text-align: left;
    font-weight: 600;
    background-color: #084C61;
    color: #FFFFFF;
  }
  .calc-calculator-wrapper td.monthly-expenses .tooltip {
    display: none;
  }
  .calc-calculator-wrapper td.monthly-present, .calc-calculator-wrapper td.monthly-sls {
    width: 100%;
  }
  .calc-calculator-wrapper td:after {
    float: left;
    content: attr(data-title);
  }
}

#container-options {
  background-color: #177E89;
  width: 100%;
  margin-bottom: 2px;
  padding: 30px 20px;
}

#container-options h2 {
  border: 0px;
  color: #FFFFFF;
  font-size: 21px;
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 15px;
  letter-spacing: 0.25px;
}

#container-options select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #FFFFFF url("../../img/angle-arrow-down.svg") no-repeat 95% center;
  background-size: contain;
  width: auto;
  /*Width of select dropdown to give space for arrow image*/
  text-indent: 0.01px;
  /* Removes default arrow from firefox*/
  text-overflow: "";
  /*Removes default arrow from firefox*/
  color: #000000;
  border-radius: 2px;
  border: 1px solid #E9EBEB;
  padding: 5px 10px;
  padding-right: 30px;
}

#container-options select.sl-select {
  background-color: #FFFFFF;
  margin-right: 10px;
  padding-right: 60px;
}

.horizontal-card p {
  color: white;
}

.horizontal-card .text-box {
  padding: 40px;
}

.horizontal-card .img-box {
  border-radius: 0 0 25px 25px;
  background-color: white;
}

@media (min-width: 768px) {
  .horizontal-card .img-box {
    border-radius: 0 25px 25px 0;
  }
}
